$tabletWidth: 1400px
$padWidth: 1100px
$phoneWidth: 415px

@mixin tablet
  @media screen and (max-width: $tabletWidth) 
    @content
@mixin pad
  @media screen and (max-width: $padWidth) 
    @content
@mixin phone 
  @media screen and (max-width: $phoneWidth) 
    @content

.shino
  .content
    background: #C1BAAC
    &__item
      background: none
      padding: 0

.carousel
  &__item
    vertical-align: middle
    max-width: 700px
    height: 350px
    @include phone
      max-width: 300px
      height: 200px
    img
      margin: 0 auto
      max-width: 100%
.ant-carousel .slick-dots-bottom
  top: 50%
  margin-top: 10%
.summary
  &__img
    padding: 50px
  &__des
    padding: 10px 50px